dialog {
  max-inline-size: 758px;
  padding: 0;
  border: none;
  border-radius: var(--m);
  .contacts-form {
    position: relative;
    padding: 60px;
    padding: clamp(0.94rem, calc(-0.40rem + 6.70vw), 3.75rem);
    h3 {
      margin-block-end: 20px;
    }
  }
  .form-modal {
    .checkbox-wrap {
      align-self: center;
    }
    .close-btn {
      --p: clamp(0.94rem, calc(0.49rem + 2.23vw), 1.88rem);
      position: absolute;
      top: var(--p);
      right: var(--p);
      border: none;
      background-color: transparent;
      cursor: pointer;
    }
  }
}

dialog::backdrop {
  background-color: hsl(215 62% 19% / .6);
}
