.anchor-block {
  overflow: hidden;
  block-size: clamp(22.13rem, calc(-0.51rem + 47.16vw), 37.69rem);
  margin-block-end: -14.6875rem;
  position: relative;
  z-index: 2000;
  background-image: url('../img/misc/anchor-block-bg.png');
  background-repeat: repeat-x;
  background-position: left top;
  background-size: 8px clamp(12.50rem, calc(2.95rem + 19.89vw), 19.06rem);
  .container {
    position: unset;
  }
  &::before {
    content: "";
    position: absolute;
    inset: 0;
    inline-size: clamp(211.75rem, calc(-3.89rem + 449.24vw), 360.00rem);
    block-size: clamp(21.25rem, calc(-0.48rem + 45.27vw), 36.19rem);
    background-image: url('../img/svg/misc/anchor-block-bottom-layer.svg');
    background-repeat: no-repeat;
    background-position: left top;
    background-size: 100%;
  }
  &::after {
    content: "";
    position: absolute;
    inset: 0;
    inline-size: clamp(211.75rem, calc(-3.89rem + 449.24vw), 360.00rem);
    block-size: clamp(18.56rem, calc(-0.35rem + 39.39vw), 31.56rem);
    background-image: url('../img/svg/misc/anchor-block-mid-layer.svg');
    background-repeat: no-repeat;
    background-position: left top;
    background-size: 100%;
  }
  &__content {
    display: grid;
    grid-template-areas: 'img h3' 'img desc';
    grid-template-columns: max-content 1fr;
    column-gap: clamp(2.50rem, calc(-5.68rem + 17.05vw), 8.13rem);
  }
  &__content::before {
    content: "";
    position: absolute;
    z-index: 1000;
    inset: 0;
    inline-size: clamp(211.75rem, calc(-3.89rem + 449.24vw), 360.00rem);
    block-size: clamp(22.13rem, calc(-0.42rem + 46.97vw), 37.63rem);
    background-image: url('../img/svg/misc/anchor-block-top-layer.svg');
    background-repeat: no-repeat;
    background-position: left top;
    background-size: 100%;
  }
  &__desc {
    grid-area: desc;
    position: relative;
    z-index: 2000;
    max-inline-size: 39.875rem;
    .btn {
      border: 1px solid var(--clr-light);
    }
  }
  h3 {
    grid-area: h3;
    align-self: end;
    position: relative;
    z-index: 2000;
    margin-block-end: .625rem;
    max-inline-size: 39.875rem;
  }
  p {
    margin-block-end: var(--m);
  }
  .img {
    position: relative;
    z-index: 10;
    grid-area: img;
    max-inline-size: clamp(25.50rem, calc(14.59rem + 22.73vw), 33.00rem);
  }
}

@media (max-width: 768px) {
  .anchor-block {
    block-size: auto;
    padding-block-end: var(--m);
    background-color: var(--clr-light);
    background-image: unset;
    &::before {
      top: 6.25rem;
    }
    &::after {
      top: 6.25rem;
    }
    &__content {
      display: flex;
      flex-direction: column;
    }
    &__content::before {
      top: 6.25rem;
    }
    h3 {
      align-self: flex-start;
    }
    .img {
      align-self: center;
    }
  }
}

@keyframes anchore_top_layer {
  0% {
    transform: translateX(-60%);
  }
  100% {
    transform: translateX(0%);
  }
}

.anchor-block__content::before {
  animation: anchore_top_layer 35s linear infinite alternate;
}

@keyframes anchore_mid_layer {
  0% {
    transform: translateX(-60%);
  }
  100% {
    transform: translateX(0%);
  }
}

.anchor-block::after {
  animation: anchore_mid_layer 80s linear infinite alternate;
}

@keyframes anchore_bottom_layer {
  0% {
    transform: translateX(-60%);
  }
  100% {
    transform: translateX(0%);
  }
}

.anchor-block::before {
  animation: anchore_bottom_layer 50s linear infinite alternate;
}
