.price-item {
  .img {
    position: relative;
    overflow: hidden;
    aspect-ratio: 1.62 / 1;
    margin-block-end: var(--m);
    border-radius: var(--m);
    a::after {
      content: "";
      position: absolute;
      inset: 0;
      background-color: transparent;
      transition: background-color var(--trans-base);
    }
    a:hover::after {
      background-color: hsl(216 88% 68% / .6);
    }
    img {
      inline-size: 100%;
      object-fit: cover;
    }
  }
  h4 {
    margin-block-end: 1px;
  }
  p {
    margin-block-end: var(--m);
  }
  .price {
    margin-block-end: 0;
  }
  a.btn {
    display: none;
  }
}

.price-item.short {
  .price {
    display: none;
  }
  a.btn {
    display: inline-block;
  }
}
